@charset "UTF-8";
:root {
  --font-family-base: "Zen Old Mincho", "Hiragino Mincho ProN", "Yu Mincho", serif;
  --font-family-second: "Inter", "Hiragino Sans", "Yu Gothic", "Noto Sans JP", sans-serif;
  --font-family-third: "Zen Kaku Gothic New", "Hiragino Kaku Gothic ProN", "Yu Gothic", "Noto Sans JP", sans-serif;
  --font-family-fourth: "Noto Sans JP", "Hiragino Kaku Gothic ProN", "Yu Gothic", sans-serif;
}

:root {
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-black: 900;
}

:root {
  --black: #000;
  --white: #fff;
  --text-color: #626262;
  --text-gray: #ACACAC;
  --main-color: #E60012;
  --main-color2: #C4A552;
  --mh_goldlabel: #C3B889;
  --bg-color: #F9F6EE;
  --line-color: #DDD;
  --line-color2: #ECECEC;
  --linear-gradient: linear-gradient(270deg, rgba(249, 246, 238, 0.00) 3.37%, var(--bg01, #F9F6EE) 29.53%);
}

:root {
  --z-index-header: 100;
  --z-index-to-top: 200;
  --z-index-float-entry: 1000;
  --z-index-drawer: 2000;
  --z-index-hamburger: 10000;
  --z-index-modal: 100000;
}

:root {
  --duration-short: 0.2s;
  --duration-base: 0.3s;
  --duration-long: 0.6s;
  --duration-fv: 0.7s;
  --ease-smooth-bounce: cubic-bezier(.48, .07, .33, .79);
}

:root {
  --cq-ref-mobile: 375;
  --cq-ref: 1440;
  --fluid-basis-mobile: (100vw - 320px) / (375 - 320);
  --fluid-basis: (100vw - 768px) / (1440 - 768);
  --fluid-basis-2: (100vw - 1440px) / (1800 - 1440);
}

body {
  color: var(--text-color);
  font-family: var(--font-family-base);
}

:root:not(:lang(ja)) .translate-none {
  display: none;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/*  */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root:not(:lang(ja)) body {
  overflow-wrap: break-word;
  hyphens: auto;
  word-break: normal;
  line-break: auto;
}

ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a,
span,
button {
  display: inline-block;
}

small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  border: 0;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

button {
  color: inherit;
}

@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
a {
  -webkit-tap-highlight-color: transparent;
}

button {
  -webkit-tap-highlight-color: transparent;
}

.float-entry-btn-sp {
  position: fixed;
  bottom: 20px;
  left: 50%;
  translate: -50% 0;
  z-index: var(--z-index-float-entry);
}
@media screen and (min-width: 768px) {
  .float-entry-btn-sp {
    display: none;
  }
}

.float-entry-btn-sp__inner {
  padding-inline: 2.0625rem 1.875rem;
  max-width: 500px;
  width: 100vw;
  margin-inline: auto;
}

.float-entry-btn-sp__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: var(--main-color);
  border-radius: 10px;
  box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.2);
  gap: 0.0625rem;
  padding-block: 0.625rem;
  position: relative;
}

.float-entry-btn-sp__link::after {
  content: "";
  position: absolute;
  top: 55%;
  translate: 0 -50%;
  right: 0.9375rem;
  width: 0.5625rem;
  height: 0.5625rem;
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
  transform: translateY(-50%) rotate(45deg);
  transition: border-top var(--duration-base) ease, border-right var(--duration-base) ease;
}

.float-entry-btn-sp__label {
  color: var(--white);
  text-align: center;
  font-family: var(--font-family-second);
  font-size: 0.6875rem;
  font-weight: var(--font-weight-bold);
  line-height: 1;
}

.float-entry-btn-sp__label-ls {
  letter-spacing: 0.09em;
}

.float-entry-btn-sp__text {
  color: var(--white);
  text-align: center;
  font-family: var(--font-family-second);
  font-size: 1.75rem;
  font-style: italic;
  font-weight: --font-weight-semibold;
  line-height: 1;
  letter-spacing: 0.14em;
  text-transform: uppercase;
}

.fv {
  height: auto;
  overflow-x: clip;
  margin-top: 3.3125rem;
}
@media screen and (min-width: 768px) {
