}
@media screen and (min-width: 768px) {
  .fv__inner {
    grid-template-columns: 1fr clamp(160px, 81.1428571429px + 10.2678571429vw, 229px);
  }
}

.fv__content {
  position: absolute;
  top: 97%;
  left: 51.5%;
  translate: -50% -50%;
