/* ===============================================
# IntersectionObserverGSAP
=============================================== */
/*  */
.js-fade-in {
  opacity: 0;
  transition: opacity 1.2s ease-in-out;
}
.js-fade-in.is-active {
  opacity: 1;
}

/*  */
.js-fade-up {
  opacity: 0;
  translate: 0 20px;
  transition: opacity 1.2s ease-in-out, translate 1.2s ease-in-out;
}
.js-fade-up.is-active {
  opacity: 1;
  translate: 0 0;
}

.job-detail__interview.js-fade-up,
.training__item.js-fade-up {
  transition: opacity 1s ease-in-out, translate 1s ease-in-out;
}

/*  */
.js-slide-left {
  opacity: 0;
  translate: -20px 0;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
}
.js-slide-left.is-active {
  opacity: 1;
  translate: 0 0;
}

/*  */
.js-fade-down {
  opacity: 0;
  translate: 0 -20px;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
}
.js-fade-down.is-active {
  opacity: 1;
  translate: 0 0;
}

/*  */
.js-slide-right {
  opacity: 0;
  translate: 10px 0;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
}
@media screen and (min-width: 768px) {
  .js-slide-right {
    translate: 20px 0;
  }
}
.js-slide-right.is-active {
  opacity: 1;
  translate: 0 0;
}

.js-scaleImg {
  overflow: hidden;
}
.js-scaleImg img {
  scale: 1.1;
  transition: scale 1.2s var(--ease-smooth-bounce), opacity 1.2s var(--ease-smooth-bounce);
  opacity: 0;
}
.js-scaleImg.is-active img {
  scale: 1;
  opacity: 1;
}

.global-expansion__img-wrap.js-scaleImg img {
  transition: scale 1.4s var(--ease-smooth-bounce), opacity 1.4s var(--ease-smooth-bounce);
}

/*  */
.js-clip-img {
  overflow: hidden;
}
.js-clip-img img {
  scale: 1.3;
  transition: scale 1s var(--ease-smooth-bounce), clip-path 1s var(--ease-smooth-bounce);
  clip-path: inset(0 100% 0 0);
}
.js-clip-img.is-active img {
  clip-path: inset(0 0 0 0);
  scale: 1;
}
@media screen and (min-width: 768px) {
  .js-clip-img img {
    transition: scale 1.2s var(--ease-smooth-bounce), clip-path 1.2s var(--ease-smooth-bounce);
  }
}

.js-clip-item {
  overflow: hidden;
  transition: scale 0.8s var(--ease-smooth-bounce), clip-path 0.8s var(--ease-smooth-bounce);
  clip-path: inset(0 100% 0 0);
}
.js-clip-item.is-active {
  clip-path: inset(0 0 0 0);
  scale: 1;
}
.js-clip-item.is-active .top-recruit__link {
  scale: 1;
}
.js-clip-item .top-recruit__link {
  scale: 1.3;
  transition: scale 1s var(--ease-smooth-bounce);
}

.js-text-animate.is-active {
  animation: leadSweep 2.8s cubic-bezier(0.25, 0.1, 0.25, 1) forwards;
  display: inline-block;
  background: linear-gradient(90deg, transparent 0%, #E60012 20%, #E60012 50%, transparent 60%) 0 0/200% 100% no-repeat, linear-gradient(#626262, #626262) 0 0/100% 100% no-repeat;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
  animation: leadSweep 2.8s cubic-bezier(0.25, 0.1, 0.25, 1) forwards;
}

@keyframes leadSweep {
  0% {
    background-position: 100% 0, 0 0;
  }
  100% {
    background-position: -100% 0, 0 0;
  }
}
